@import "../../sass/mixin";
@import "../../sass/variables";

.from__input {
  padding: 13px;
  outline: none;
  margin-right: 10px;
  margin-bottom: 30px;
}

.button {
  display: inline-flex;
  width: 136px;
  height: 44px;
  padding: 14px 10px 12px 10px;

  border-radius: 5px;

  background-color: $light-text-color;

  text-transform: uppercase;
  font-family: "Roboto";
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  justify-content: center;

  &:hover {
    background-color: $accent-color;
    color: $light-text-color;
    border: none;
    cursor: pointer;
  }

  transition: background-color $cubic;
}
